@import 'variables';
@import 'mixins';
@import 'colors';
@import 'utilities';

.markdown-body {
	box-sizing: border-box;
	min-width: 200px;
	max-width: 980px;
	margin: 0 auto;
	padding: 45px;
}

@media (max-width: 767px) {
	.markdown-body {
		padding: 15px;
	}
}

// Nav
.nav--cv {
	@include flex(row nowrap, space-between, center, center);
	@extend .hideOnPrint;
	background: white;
	height: 50px;
	width: calc(100% - 106px);
	padding: 0 53px;
	position: fixed;
	top: 0;
	z-index: 1000;
	@media screen and (max-width: $breakpoint-m) {
		width: calc(100% - 30px);
		padding: 0 15px;
	}
	a {
		@include flex(row nowrap, center, center, center);
		height: 35px;
		font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
		text-decoration: none;
		color: $accent-color--cv;
		font-weight: bold;
		border-bottom: 3px solid transparent;
		transition: border-bottom 125ms;
		@media screen and (max-width: $breakpoint-m) {
			margin: 0;
		}
		&:hover {
			border-bottom: 3px solid $accent-color--cv;
		}
	}
}

// Modifying base element styling
html,
body {
	margin: 0;
	width: 100%;
}

hr {
	display: none;
	@media screen and (max-width: $breakpoint-m) {
		display: block;
	}
}

.markdown-body {
	h2 {
		@extend .pageBreakBefore;
		position: sticky;
		top: 49px;
		height: 50px;
		background: white;
		color: $accent-color--cv;
		z-index: 900;
	}
	h3 {
		position: sticky;
		top: 99px;
		padding: $space-xs 0;
		border-bottom: 1px solid #eaecef;
		background: white;
		z-index: 800;
	}
	a {
		font-weight: 600;
	}
	ul {
		list-style: none;
		padding-left: 0;
	}
	table {
		display: table;
		table-layout: fixed;
		width: 100%;
		th {
			text-align: left;
		}
		th:first-of-type,
		td:first-of-type {
			width: 25%;
			@media print {
				width: initial;
			}
		}
	}
	@media print {
		font-size: 14px;
	}
}

// CV specific utility classes
.focus {
	background: #dfe2e5;
	border: 1px solid #dfe2e5;
	border-radius: $border-radius;
	padding: $space-s;
	&:before {
		content: 'ðŸ“Œ ';
	}
}

.info {
	background: #dfe2e5;
	border: 1px solid #dfe2e5;
	border-radius: $border-radius;
	padding: $space-s;
	&:before {
		content: 'ðŸ’¡ ';
	}
}

.icon {
	display: inline-block;
	width: 1.5rem;
}

.border--std {
	border: 1px solid $light-gray--cv;
}

#scrollToTopBtn {
	@include boxShadow--tight(0.25);
	@extend .border--std;
	@extend .hideOnPrint;
	position: relative;
	z-index: 1000;
	box-sizing: border-box;
	display: inline-block;
	background: white;
	line-height: 1rem;
	font-size: $font-size-l;
	color: $bright-color;
	position: fixed;
	bottom: 15px;
	right: 15px;
	text-align: center;
	outline: 0;
	padding: 1rem;
	transition: all $trans-fast;
	span {
		position: relative;
		left: -1px;
		top: -2px;
	}
	&:hover {
		background: $light-gray--cv;
		border-radius: 15%;
		@media screen and (max-width: $breakpoint-m) {
			border: none;
		}
	}
}

// Introduction
.markdown-body.content__section--introduction {
	.introduction {
		&__header {
			margin-top: $space-m !important;
		}
		&__photoAndPersonalData {
			@include grid(1fr 1.5fr, $space-m, 400px, $space-m);
			margin-top: $space-m;
			@media screen and (max-width: $breakpoint-m) {
				@include grid(1fr, $space-m, 400px 400px, $space-m);
			}
			@media print {
				@include grid(1fr, $space-m, 200px 200px, $space-m);
			}
		}
		&__photo {
			height: 100%;
			min-width: 320px;
			@media screen and (max-width: $breakpoint-m) {
				height: auto;
				width: 50%;
				justify-self: center;
			}
			@media screen and (max-width: $breakpoint-s) {
				height: auto;
				width: 100%;
				justify-self: center;
			}
			@media print {
				height: 200px;
				width: auto;
				min-width: initial;
				justify-self: center;
			}
		}
		&__personalData {
			// TODO Make table 100% width. Maybe with grid?
			width: 100%;
			height: 100%;
			td:first-of-type,
			th:first-of-type {
				width: 35%;
			}
			@media screen and (max-width: $breakpoint-s) {
				@include mobileTableScroll();
				height: auto;
				width: auto;
			}
		}
		&__text {
			clear: both;
		}
	}
}

// Skills

.markdown-body.content__section--skills {
	.skills {
		&__technologies {
			display: block;
			background: white;
			padding: 0;
			tr {
				background: white;
			}
			th,
			td {
				@include textShadow();
				color: $dark-gray--cv;
				font-weight: 600;
				text-align: left;
				width: 900px;
				@media print {
					text-shadow: none;
				}
			}
			th {
				background: $light-gray--cv;
				color: $dark-gray--cv;
			}
			// Language Icons
			.langIcon {
				font-size: $font-size-m;
				// color: $dark-gray--cv !important;
				font-weight: normal;
				display: inline-block;
				width: 2rem;
			}
			.fa-html5 {
				color: orangered;
			}
			.fa-css3 {
				color: steelblue;
			}
			.fa-sass {
				color: palevioletred;
			}
			.fa-js {
				color: gold;
			}
			.fa-git {
				color: rgb(212, 83, 36);
			}
			.fa-react {
				color: #61dafb;
			}
			.fa-angular {
				color: #b52e31;
			}
			.fa-node-js {
				color: #6cc24a;
			}
			.fa-adobe {
				color: #ff0000;
			}
			.fa-figma {
				color: #f89075;
			}
			// Progress bars
			#skillLevel_HTML {
				@include progressBar--cv(95%);
			}
			#skillLevel_CSS {
				@include progressBar--cv(95%);
			}
			#skillLevel_SCSS {
				@include progressBar--cv(75%);
			}
			#skillLevel_JavaScript {
				@include progressBar--cv(70%);
			}
			#skillLevel_Git {
				@include progressBar--cv(75%);
			}
			#skillLevel_React {
				@include progressBar--cv(55%);
			}
			#skillLevel_Angular {
				@include progressBar--cv(15%);
			}
			#skillLevel_NodeJS {
				@include progressBar--cv(55%);
			}
			#skillLevel_Photoshop {
				@include progressBar--cv(75%);
			}
			#skillLevel_Figma {
				@include progressBar--cv(85%);
			}
		}
		&__certificates {
			@extend .pageBreakBefore;
			.certificates {
				&__table {
					@media screen and (max-width: $breakpoint-m) {
						@include mobileTableScroll();
					}
					th:nth-of-type(3),
					td:nth-of-type(3) {
						width: 20%;
					}
					th:nth-of-type(4),
					td:nth-of-type(4) {
						width: 20%;
					}
				}
			}
		}
	}
}

// Projects
.markdown-body.content__section--projects {
	.project {
		@extend .border--std;
		&:not(:first-of-type) {
			@extend .pageBreakBefore;
		}
		padding: $space-m;
		margin-bottom: $space-m;
		ul {
			list-style-type: disc;
			list-style-position: inside;
		}
		@media screen and (max-width: $breakpoint-m) {
			border: 0px solid $dark-gray--cv;
			padding: 0;
			table {
				@include mobileTableScroll();
			}
		}
		@media print {
			border: 0px solid $dark-gray--cv;
			padding: 0;
		}
	}
	.playgroundProjects {
	}
	.playgroundProjects__container {
		@include grid(1fr 1fr, $space-m, auto, $space-m);
		@media screen and (max-width: $breakpoint-m) {
			@include grid(1fr, $space-m, auto, $space-m);
		}
	}
	.playgroundProject {
		@extend .border--std;
		padding: $space-s;
	}
}

// Principles
.markdown-body.content__section--principles {
	dt {
		font-style: initial;
		margin-bottom: $space-s;
	}
	dd {
		@media print {
			margin-bottom: $space-xs;
		}
	}
}

// Bio
.markdown-body.content__section--bio {
	.bio {
		&__career {
			th:first-of-type,
			td:first-of-type {
				width: 20%;
				font-weight: bold;
				font-variant-numeric: tabular-nums;
				@media print {
					width: 30%;
				}
			}
			@media screen and (max-width: $breakpoint-m) {
				@include mobileTableScroll();
			}
		}
		&__activities {
			@extend .pageBreakBefore;
			.activities {
				&__container {
					@include grid(1fr 1fr, $space-m, auto, $space-m);
					@media screen and (max-width: $breakpoint-m) {
						@include grid(1fr, $space-m, auto, $space-m);
					}
					.activity {
						@extend .border--std;
						padding: $space-s $space-m;
						&:nth-child(3) {
							grid-column: 1 / 3;
							@media screen and (max-width: $breakpoint-m) {
								grid-column: 1 / 2;
							}
						}
					}
				}
			}
		}
	}
}

// Contact
.markdown-body.content__section--contact {
	@extend .hideOnPrint;
}
